
*{
  margin: 0px;
  padding: 0px;
}
body{
  background-color: #0C1624;
  background-image: none;
  background-repeat: no-repeat;
  background-attachment:fixed;
  background-size: cover;
}
a{
  text-decoration: none;
  /*color: #074080;*/
  cursor: pointer;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.default{
  background-color: none;
  color:#FFF;
  -webkit-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
}
.scrolled{
  background-color: #fff;
  color:#2c3e50;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  -webkit-transition: all 1s ease 0s;
  transition: all 1s ease 0s;
}
#title{
  width: 100%;
  /*height: 100%;*/
  line-height: 60px;
  /*background-color: #252C30;*/
  /*background-image: url(../static/bg_01_small.png);*/
  
  display: -webkit-box;
  
  display: -ms-flexbox;
  
  display: flex;
  /*border-bottom: 5px solid #074080;*/
  position: fixed;
  z-index: 999;
  font-weight: bold;
}
#title>.left{
  text-align: left;
  margin-left: 10%;
  
  /*float: left;*/
}
#title>.right{
  position: absolute;
  right: 10%;
  overflow: hidden;
}
#title>.right>a#CL {
  /*padding: 0 0 0 40px;*/
  /*background-color: #fff;*/
  /*text-decoration: none;*/
  /*border-radius: 0px 0px 5px 5px;*/
}
.router-link-exact-active{
  /*background-color: #FFF;*/
  text-decoration: underline;
}
#title>.right>i.icon-category{
  margin: 0 60px;
  display: none;
  cursor: pointer;
  /*text-align: right;*/
}
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
#changeL{
  padding: 0 20px;
  cursor: pointer;
  /*background-color: #074080;*/
  /*color: #fff;*/
  display: inline-block;
}
#changeL>div{
  display: inline-block;
}
#changeL>.selected{
  text-decoration: underline;
}
#changeL:hover{
  background-color: rgba(255,255,255,0.5);
  color: #000;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.view{
  padding-top: 60px;
}
@media screen and (max-width: 800px) {
body{
  /*background-image: url(./assets/image/bg_01_small.png);*/
}
#title>.right{
  right: 0px;
}
/*#title>.right>#CL{
  display: none;
  background-color: #fff;
  color: #fff;
  padding: 0px;
}*/

/*#title>.right>i.icon-category{
  display: inline-block;
}*/
#title>.right:hover #CL{
  display: block;
}
}




