
#project_detail {
    width: 70%;
    /*max-width: 900px;*/
    /*height: 100%;*/
    position: absolute;
    margin: 0% 10%;
    padding: 5%;
    text-align: left;
    background-color: #fff;
}
.back {
    text-align: center;
    width: 120px;
    height: 50px;
    line-height: 50px;
    border-radius: 20px;
    background-color: #FC3941;
    color: #fff;
    margin: auto;
    font-size:1.2em;
}
img {
    display: inline-block;
    height: auto;
    max-width: 100%;
}
@media screen and (max-width: 800px) {
#project_detail {
    width: 90%;
    margin: 0;
}
}




